import React, { useState } from 'react';
import s from './Paginator.module.css';

let Paginator = ({totalUsersCount,pageSize,currentPage,onPageChange,portionSize = 10}) => {
    let pagesCount = Math.ceil(totalUsersCount / pageSize);

    let pages = [];
    for (let i = 1; i < pagesCount; i++) {
        pages.push(i);
    };

    let portionCount = Math.ceil(pagesCount / pageSize);
    let [portionNumber,setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;

    return <div className={s.paginator}>
        <div>
        {
            portionNumber > 1 && <button onClick={ () => {setPortionNumber(portionNumber - 1)}}> PREVIOUS</button> 
        }
        </div>
        <div className={s.pages}>
            {pages
            .filter( p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
            .map((p) => {
            
               return <span className={currentPage === p && s.selectedPage}
                    onClick={(e) => { onPageChange(p); }}
                    key={p}
                    >{p}</span>

            })}
        </div>
        
        {
            portionCount > portionNumber && <button onClick={() =>{ setPortionNumber(portionNumber + 1)}}> NEXT </button>
        }
     
    </div>

}

export default Paginator;