import React from 'react';
import Paginator from '../../files/common/paginator/Paginator';
import User from './User';
let Users = ({currentPage, onPageChange, totalUsersCount, pageSize,users,followingInProgress,follow,unfollow}) => {


    return <div>
     <Paginator currentPage={currentPage} onPageChange={onPageChange} totalUsersCount={totalUsersCount} pageSize={pageSize} />
        {users.map(u =><User key={u.id} user={u} followingInProgress={followingInProgress} unfollow={unfollow} follow={follow}/>)}

    </div>

}

export default Users;